import Router from "vue-router";
import Vue from "vue";

import Home from "../routerPage/Home";
import Article from "../routerPage/Article";
import Message from "../routerPage/Message";



Vue.use(Router);

export default new Router({
    mode: "history",
    routes: [
        {
            path: "/__kuangJiaKaiFa/vueAppDemo/bok/dist/index.html",
            name: "home",
            component: Home
        },
        {
            path: "/bok",
            name: "home",
            component: Home
        },
        {
            path: "/bok/article",
            name: "article",
            component: Article
        },
        {
            path: "/bok/message",
            name: "message",
            component: Message
        }
    ]
})